@import url(https://api.mapbox.com/mapbox-gl-js/v1.12.0/mapbox-gl.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mapbox-gl-draw_union {
    background-image: url(/mapbox-gl-draw-additional-tools/static/media/union.2cd460cb.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
}
.mapbox-gl-draw_buffer {
    background-image: url(/mapbox-gl-draw-additional-tools/static/media/buffer.2cdd7b1b.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
}
.mapbox-gl-draw_copy {
    background-image: url(/mapbox-gl-draw-additional-tools/static/media/copy.be548b09.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 60%;
}

h1,
p {
  font-family: Lato;
}

.map-wrapper {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  border-radius: inherit;
}
#map {
  width: 100%;
  height: 100%;
}

